import React from 'react';
import { Box, Typography, Button, Card, CardMedia, CardContent, CardActions, Chip } from '@material-ui/core';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import PhoneIcon from '@material-ui/icons/Phone';
import Rating from '@material-ui/lab/Rating';

import useStyles from './styles';
 
const PlaceDetails = ({ place, selected, refProp }) => {

    if(selected) refProp?.current?.scrollIntoView({behavior: "smooth", block: "start" });
    
    const classes = useStyles();

    return (
            <Card elevation={6}>
                <CardMedia 
                    style={{height: 350 }}
                    image={place.photo ? place.photo.images.large.url : 'https://www.foodserviceandhospitality.com/wp-content/uploads/2016/09/Restaurant-Placeholder-001.jpg'}
                    title={place.name}
                />
                <CardContent>
                    <Typography gutterButtom variant="h5">{place.name}</Typography>
                    <Box display="flies" justifyContent="space-between">
                        <Rating value={Number(place.rating)} readOnly />
                        <Typography gutterBottom variant="subtitle">out of{place.num_reviews}reviews</Typography>
                    </Box>
                    <Box display="flies" justifyContent="space-between">
                        <Typography variant="subtitle">Price</Typography>
                        <Typography gutterBottom variant="subtitle">{place.price_level}</Typography>
                    </Box>
                    <Box display="flies" justifyContent="space-between">
                        <Typography variant="subtitle">Ranking</Typography>
                        <Typography gutterBottom variant="subtitle">{place.ranking}</Typography>
                    </Box>
                    {place?.cuisines?.map(({ name }) => (
                        <Chip key={name}  size="small"  label={name}  className={classes.chip} />
                    ))}

                    {place?.address && (
                        <Typography gutterBottom variant="subtitle2"  color="textSecondary"  className={classes.subtitle}>
                            <LocationOnIcon /> {place.address}
                        </Typography>
                    )}

                    {place?.phone && (
                        <Typography gutterBottom variant="subtitle2"  color="textSecondary"  className={classes.spacing}>
                            <PhoneIcon /> {place.phone}
                        </Typography>
                    )}

                    <CardActions>
                        <Button size="small"  color="primary"  onClick={() => window.open(place.web_url, '_blank')}>
                            Trip Advisor
                        </Button>
                        <Button size="small"  color="primary"  onClick={() => window.open(place.website, '_blank')}>
                            Website
                        </Button>
                    </CardActions>
                </CardContent>
            </Card>
    );
}

export default PlaceDetails;
